<template>
  <el-upload
    action=""
    :style="uploadStyle"
    :show-file-list="false"
    :before-upload="beforeImgageUpload"
    :auto-upload="false"
    :limit="1"
    accept=".png,.jpg,.jpeg,.webp"
  >
    <div class="uploader-content" :style="imagePreviewStyle">
      <template v-if="imageUrl">
        <img :src="imageUrl" class="image" />

        <span class="image-uploader-actions" @click.stop>
          <span @click="deleteImage">
            <i class="el-icon-delete"></i>
          </span>
        </span>
      </template>

      <div v-else>
        <i class="image-uploader-icon"></i>

        <h5 class="upload-tips">{{ uploadTips }}</h5>
      </div>
    </div>
  </el-upload>
</template>

<script>
export default {
  props: {
    uploadPosition: { type: String, default: 'center' },
    imagePreviewSize: { type: String, default: '152px' },
    uploadTips: String
  },

  data () {
    return {
      imageUrl: ''
    }
  },

  computed: {
    uploadStyle () {
      return {
        textAlign: this.uploadPosition
      }
    },

    imagePreviewStyle () {
      return {
        width: this.imagePreviewSize,
        height: this.imagePreviewSize
      }
    }
  },

  methods: {
    beforeImgageUpload (file) {
      const isJPG = [
        'image/png',
        'image/jpg',
        'image/jpeg',
        'image/webp'
      ].includes(file.type)

      const isLt2M = file.size / 1024 / 1024 < 10

      if (!isJPG) {
        this.$message.error(
          'The uploaded image must be in the format of png/jpg/jpeg/webp.'
        )
      }

      if (!isLt2M) {
        this.$message.error(
          'The size of the uploaded image cannot exceed 10MB.'
        )
      }

      const isSize = new Promise((resolve, reject) => {
        const img = new Image()
        img.onload = () => {
          const width = img.width
          const height = img.height
          if (
            width >= 256 &&
            width <= 4096 &&
            height >= 256 &&
            height <= 4096
          ) {
            this.imageUrl = img.src
            this.$emit('change', file.raw)
            resolve()
          } else {
            this.$message.error(
              'The dimensions of the uploaded image must be between 256 and 4096 pixels in both length and width.'
            )
            reject(new Error())
          }
        }
        img.src = URL.createObjectURL(file.raw)
      })
      return isJPG && isLt2M && isSize
    },

    deleteImage () {
      console.log('del')
    }
  }
}
</script>

<style scoped lang="less">
@import "@/styles/variable.less";

.uploader-content {
  background: linear-gradient(134deg, #f4f1fe 0%, #faf9ff 100%);
  border-radius: 16px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  margin: 0 auto;
}

.image-uploader-icon {
  display: inline-block;
  width: 24px;
  height: 24px;
  background-image: url("~@/assets/plus-icon.png");
}

.upload-tips {
  font-weight: 400;
  font-size: 14px;
  color: #8f8f8f;
  line-height: 24px;
  text-align: center;
}

.image {
  width: 100%;
  height: 100%;
  display: block;
}

.image-uploader-actions {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  cursor: default;
  text-align: center;
  color: #fff;
  opacity: 0;
  font-size: 20px;
  background-color: rgba(0, 0, 0, 0.5);
  transition: opacity 0.3s;

  span {
    display: none;
    cursor: pointer;
  }

  &::after {
    display: inline-block;
    content: "";
    height: 100%;
    vertical-align: middle;
  }

  &:hover {
    opacity: 1;

    span {
      display: inline-block;
    }
  }
}
</style>
