<template>
  <div>
    <el-row type="flex">
      <el-col :span="10" class="generate-conditon">
        <el-form
          :model="formValues"
          ref="form1"
          :rules="formRules"
          size="small"
          label-position="top"
        >
          <FormBlock label="Upload a model image" prop="imageRaw">
            <UploadImage
              upload-position="center"
              @change="handleChangeImage"
            ></UploadImage>
          </FormBlock>

          <FormBlock
            label="Describe the desired clothing for the model or upload a clothing image"
            prop="backgroundDesc"
          >
            <div class="form-item--desc">
              <el-input
                v-model="formValues.backgroundDesc"
                placeholder="e.g. Change the model's coat to a gray denim jacket."
                type="textarea"
                class="desc-input"
                maxlength="200"
                resize="none"
                :rows="5"
              >
              </el-input>

              <UploadImage
                upload-position="right"
                image-preview-size="128px"
                @change="handleChangeBackgroundImage"
              ></UploadImage>
            </div>
          </FormBlock>
        </el-form>

        <ViewBlock>
          <el-form
            :model="formValues"
            ref="form2"
            :rules="formRules"
            label-width="250px"
            size="small"
          >
            <el-form-item label="Size selection" prop="size">
              <el-select v-model="formValues.size" placeholder="Size selection">
                <el-option
                  v-for="item in sizeOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="Number of images to generate" prop="number">
              <div class="radio-group-wrap">
                <el-radio-group v-model="formValues.number">
                  <el-radio-button
                    v-for="item in numberOptions"
                    :key="item"
                    :label="item"
                  >
                  </el-radio-button>
                </el-radio-group>
              </div>
            </el-form-item>

            <el-form-item label-width="0" prop="agree">
              <div class="checkbox-wrap">
                <el-checkbox v-model="formValues.agree">
                  Read and agree to the
                </el-checkbox>

                <UserRulesDialog
                  v-model="showUserRulesDialog"
                  @agree="handleDialogAgree"
                >
                  <strong
                    :class="formValues.agree && 'rule-button--actived'"
                    class="rule-button"
                    @click="handleUserRulesClick"
                  >
                    &nbsp;AI User Rules.
                  </strong>
                </UserRulesDialog>
              </div>
            </el-form-item>
          </el-form>

          <el-button type="primary" class="generate-button">
            Click to generate
          </el-button>
        </ViewBlock>
      </el-col>

      <el-col :span="14" class="generate-images">
        <ViewBlock :style="contentStyle">
          <el-row
            :gutter="10"
            type="flex"
            align="middle"
            justify="space-between"
            class="generate-images-row"
          >
            <el-col
              v-if="formValues.number === 1"
              :span="24"
              class="generated-image-wrap"
            >
              <el-skeleton
                class="generated-image--single generated-image-skeleton"
                :loading="generateLoading"
                :animated="generateAnimated"
              >
                <template #template>
                  <el-skeleton-item
                    variant="image"
                    class="generated-image--single"
                  />
                </template>

                <template>
                  <el-image
                    :src="srcList[0] || ''"
                    :preview-src-list="srcList"
                    fit="fill"
                    class="generated-image--single"
                  >
                  </el-image>
                </template>
              </el-skeleton>
            </el-col>

            <template v-else-if="formValues.number === 2">
              <el-col
                v-for="i in formValues.number"
                :span="24"
                :key="i"
                class="generated-image-wrap generated-image-wrap--duo"
              >
                <el-skeleton
                  class="generated-image--duo generated-image-skeleton"
                  :loading="generateLoading"
                  :animated="generateAnimated"
                >
                  <template #template>
                    <el-skeleton-item
                      variant="image"
                      class="generated-image--duo"
                    />
                  </template>

                  <template>
                    <el-image
                      :src="srcList[i - 1] || ''"
                      :preview-src-list="srcList"
                      fit="fill"
                      class="generated-image--duo"
                    >
                    </el-image>
                  </template>
                </el-skeleton>
              </el-col>
            </template>

            <template v-else>
              <el-col
                v-for="i in formValues.number"
                :span="12"
                :key="i"
                class="generated-image-wrap"
              >
                <el-skeleton
                  class="generated-image--plural generated-image-skeleton"
                  :loading="generateLoading"
                  :animated="generateAnimated"
                >
                  <template #template>
                    <el-skeleton-item
                      variant="image"
                      class="generated-image--plural"
                    />
                  </template>

                  <template>
                    <el-image
                      :src="srcList[i - 1] || ''"
                      :preview-src-list="srcList"
                      fit="fill"
                      class="generated-image--plural"
                    >
                    </el-image>
                  </template>
                </el-skeleton>
              </el-col>
            </template>
          </el-row>
        </ViewBlock>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import FormBlock from '@/components/FormBlock.vue'
import UploadImage from '@/components/UploadImage.vue'
import ViewBlock from '@/components/ViewBlock.vue'
import UserRulesDialog from '@/components/UserRulesDialog.vue'

export default {
  components: { FormBlock, UploadImage, ViewBlock, UserRulesDialog },

  data () {
    return {
      formValues: {
        imageRaw: null,
        backgroundDesc: '',
        backgroundRow: null,
        size: '768x1024',
        number: 1,
        agree: false
      },

      formRules: {},

      sizeOptions: [
        { value: '768x1024', label: 'Illustration 3:4 (768x1024)' },
        { value: '1024x1024', label: 'Avatar 1:1 (1024x1024)' },
        { value: '720x1280', label: 'Wallpaper 9:16 (720x1280)' },
        { value: '1152x768', label: 'Illustration 3:2 (1152x768)' },
        { value: '1280x720', label: 'Movie 16:9 (1280x720)' },
        { value: '1024x768', label: 'Article illustration 4:3 (1024x768)' }
      ],

      numberOptions: [1, 2, 3, 4],

      showUserRulesDialog: false,

      srcList: [],

      generateLoading: true,
      generateAnimated: false,

      contentHeight: null
    }
  },

  computed: {
    contentStyle () {
      return {
        height: `${this.contentHeight}px`
      }
    }
  },

  methods: {
    handleChangeImage (imageRaw) {
      this.formValues.imageRaw = imageRaw
    },

    handleChangeBackgroundImage (imageRaw) {
      this.formValues.backgroundRow = imageRaw
    },

    handleUserRulesClick () {
      this.showUserRulesDialog = true
    },

    handleDialogAgree () {
      this.formValues.agree = true
    }
  },

  mounted () {
    this.contentHeight = document.querySelector(
      '.el-col.generate-conditon'
    ).scrollHeight
  }
}
</script>

<style lang="less" scoped>
@import "@/styles/variable.less";

.generate-conditon {
  padding-right: 10px;
}

.generate-images {
  padding-left: 10px;
}

.generate-images-row {
  height: 100%;
  flex-wrap: wrap;
}

.form-item--desc {
  display: flex;
  justify-content: space-between;
}

.desc-input {
  width: calc(100% - 140px);

  ::v-deep .el-textarea__inner {
    height: 132px;
  }
}

.radio-group-wrap {
  text-align: right;
}

.checkbox-wrap {
  text-align: center;
}

.rule-button {
  cursor: pointer;

  &:hover {
    color: @color-primary;
  }
}

.el-button {
  width: 100%;
}

.generated-image-wrap {
  text-align: center;

  &:nth-of-type(n + 3) {
    margin-top: 20px;
  }
}

.generated-image-wrap--duo:nth-of-type(n + 2) {
  margin-top: 20px;
}

.generated-image-skeleton {
  display: inline-block;
}

.generated-image--single {
  width: @image-size * 2.5;
  height: @image-size * 2.5;
  border-radius: @border-radius-medium;
}

.generated-image--duo {
  width: @image-size * 1.45;
  height: @image-size * 1.45;
  border-radius: @border-radius-min * 2;
}

.generated-image--plural {
  width: @image-size * 1.3;
  height: @image-size * 1.3;
  border-radius: @border-radius-min * 1.5;
}
</style>
