<template>
  <el-popover
    v-model="compVisible"
    placement="top"
    width="600"
    title="AI Privacy Agreement"
    trigger="manual"
    popper-class="user-rules-dialog"
  >
    <div class="user-rules-content">XXX</div>

    <p class="user-rules-buttons">
      <el-button size="small" @click="handleDialogCancel">取消</el-button>

      <el-button type="primary" size="small" @click="handleDialogAgree">
        确定
      </el-button>
    </p>

    <template #reference>
      <slot></slot>
    </template>
  </el-popover>
</template>

<script>
export default {
  model: {
    prop: 'visible',
    event: 'change'
  },

  props: {
    visible: {
      type: Boolean,
      required: true
    }
  },

  computed: {
    compVisible: {
      set (val) {
        this.$emit('change', val)
      },
      get () {
        return this.visible
      }
    }
  },

  methods: {
    handleDialogCancel () {
      this.compVisible = false
    },

    handleDialogAgree () {
      this.$emit('agree', true)
      this.compVisible = false
    }
  }
}
</script>

<style lang="less">
.user-rules-dialog {
  .el-popover__title {
    font-size: 16px;
  }

  .user-rules-content {
    font-size: 14px;
  }

  .el-popover__title,
  .user-rules-buttons {
    text-align: center;
  }
}
</style>
