<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
export default {
  methods: {
    postPageHeight () {
      if (window.parent !== window) {
        window.parent.postMessage(document.documentElement.scrollHeight, '*')
      }
    }
  },

  mounted () {
    this.postPageHeight()
    window.addEventListener('resize', this.postPageHeight)
  },

  destroyed () {
    window.removeEventListener('resize', this.postPageHeight)
  }
}
</script>

<style lang="less" scoped>
#app {
  width: 1112px;
  margin: 0 auto;
}
</style>
