<template>
  <div>
    <el-row type="flex">
      <el-col :span="10" class="generate-conditon">
        <el-form
          :model="formValues"
          ref="form1"
          :rules="formRules"
          size="small"
          label-position="top"
        >
          <FormBlock label="Upload a product image" prop="imageRaw">
            <UploadImage
              upload-position="center"
              image-preview-size="486px"
              @change="handleChangeImage"
            ></UploadImage>
          </FormBlock>
        </el-form>

        <ViewBlock>
          <el-button type="primary" class="generate-button">
            Click to generate
          </el-button>
        </ViewBlock>
      </el-col>

      <el-col :span="14" class="generate-images">
        <ViewBlock :style="contentStyle">
          <el-row
            :gutter="10"
            type="flex"
            align="middle"
            justify="space-between"
            class="generate-images-row"
          >
            <el-col :span="24" class="generated-image-wrap">
              <el-skeleton
                class="generated-image--single generated-image-skeleton"
                :loading="generateLoading"
                :animated="generateAnimated"
              >
                <template #template>
                  <el-skeleton-item
                    variant="image"
                    class="generated-image--single"
                  />
                </template>

                <template>
                  <el-image
                    :src="srcList[0] || ''"
                    :preview-src-list="srcList"
                    fit="fill"
                    class="generated-image--single"
                  >
                  </el-image>
                </template>
              </el-skeleton>
            </el-col>
          </el-row>
        </ViewBlock>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import FormBlock from '@/components/FormBlock.vue'
import UploadImage from '@/components/UploadImage.vue'
import ViewBlock from '@/components/ViewBlock.vue'

export default {
  components: { FormBlock, UploadImage, ViewBlock },

  data () {
    return {
      formValues: {
        imageRaw: null
      },

      formRules: {},

      srcList: [],

      generateLoading: true,
      generateAnimated: false,

      contentHeight: null
    }
  },

  computed: {
    contentStyle () {
      return {
        height: `${this.contentHeight}px`
      }
    }
  },

  methods: {
    handleChangeImage (imageRaw) {
      this.formValues.imageRaw = imageRaw
    }
  },

  mounted () {
    this.contentHeight = document.querySelector(
      '.el-col.generate-conditon'
    ).scrollHeight
  }
}
</script>

