@import "@/styles/variable.less";

.generate-conditon {
  padding-right: 10px;
}

.generate-images {
  padding-left: 10px;
}

.generate-images-row {
  height: 100%;
  flex-wrap: wrap;
}

.checkbox-wrap {
  text-align: center;
}

.rule-button {
  cursor: pointer;

  &:hover {
    color: @color-primary;
  }
}

.generate-button {
  width: 100%;
}

.generated-image-wrap {
  text-align: center;
}

.generated-image-skeleton {
  display: inline-block;
}

.generated-image--single {
  width: @image-size * 2.4;
  height: @image-size * 2.4;
  border-radius: @border-radius-medium;
}
</style>
